<template>
  <span @click="getVerCode">
    {{ content }}
  </span>
</template>
<script>
export default {
  name: 'c-countdown',
  data() {
    return {
      content: '',
      canClick: true,
      second: 0,
      verCodeFlag: false
    }
  },
  props: {
    phoneCode: {
      type: String,
      default: ''
    },
    totalTime: {
      type: Number,
      default: 60
    },
    getVerUrl: {
      type: String,
      default: ''
    },
    isMobileExists: {
      type: Boolean,
      default: false
    },
    isGoRegistered: {
      type: Boolean,
      default: false
    }
  },
  created() {
    this.second=this.totalTime
  },
  methods: {
    getVerUrlHTTP: function() {
      if (!this.canClick) return;
      this.canClick=false;//
      this.content=`(${this.second}s)`;
      let clock = window.setInterval(() => {
        this.second--;
        this.content=`(${this.second}s)`;
        if (this.second < 0) {
          window.clearInterval(clock);
          this.content='';
          this.second=60;
          this.canClick=true;
        }
      },1000);
      this.$http.post(this.getVerUrl,{'mobile': this.phoneCode}).then(response => {
        if (response.data.success){
          this.$emit('verCodeFlag',this.verCodeFlag);//
        } else {
          this.$toast(response.data.errorMessage)
        }
      }, response => {
        console.log(response)
      });
    },
    getVerCode() {
      if (!this.phoneCode) {
        this.$toast('');
        return
      }
      //
      if (this.isMobileExists) {
        this.$http.post('/api/user/isMobileExists',{'mobile': this.phoneCode}).then(response => {
          if (this.isGoRegistered){
            if (response.data.data){
              this.getVerUrlHTTP();
            } else {
              this.$dialog.confirm({
                title: '',
                message: '',
                messageAlign: 'center'
              }).then(() => {
                this.$router.push({name: ''})
              }).catch(() => {})
            }
          } else {
            if (!response.data.data){
              this.getVerUrlHTTP();
            } else {
              this.$toast('')
            }
          }
        },response => {
          console.log(response)
        })
      } else {
        this.getVerUrlHTTP();
      }
    }
  }
}
</script>
<style scoped lang="scss">
span{
  color: $orange-dark;
  font-size: 15px;
}
</style>
